/* global google*/
import React, { Component, useState } from 'react';
import Map from './Map'
import {MapFlexGrow} from '../styles'

// const refs = {
//   map: undefined,
// }
export default class MapContainer extends Component {
  
  state = {
    mapRef: null,
  }
  
  onMapMounted = () => ref => {
    this.setState = {mapRef: ref}
    console.log("just set map ref state")
  }

  fetchPlaces = () => {
    let places;
    const bounds = this.state.mapRef.getBounds();
    const service = new google.maps.places.PlacesService(this.state.mapRef.context.__SECRET_MAP_DO_NOT_USE_OR_YOU_WILL_BE_FIRED);
    const request = {
        bounds: bounds,
        type: ['hotel']
    };
    service.nearbySearch(request, (results, status) => {
        if (status === google.maps.places.PlacesServiceStatus.OK) {
          console.log(results);
          //updatePlaces(results);
        }
    })
  }
  



   render() {      
   return(
      <MapFlexGrow>
        <Map
          hasOneDest={this.props.hasOneDest}
          hasMultipleDest={this.props.hasMultipleDest}
          //hasFriends={this.props.hasFriends}
          hasRealFriends={this.props.hasRealFriends}
          // onMapMounted={this.onMapMounted}
          // setMapRef={this.onMapMounted}
          // fetchPlaces={this.fetchPlaces}
          googleMapURL={"https://maps.googleapis.com/maps/api/js?key=" + process.env.REACT_APP_GOOGLE_API_KEY + "&v=3.exp&libraries=geometry,drawing,places"}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `100%`, width: `100%` }} />} //this is where to change the container size
          mapElement={<div style={{ height: `100%` }} />}
          destinations={this.props.destinations}
          friends={this.props.friends}
        />
      </MapFlexGrow>
   );
    
   }
};






