/* global google */
import React from "react";
import {Marker, InfoWindow} from 'react-google-maps';
import DistanceContainer from "./DistanceContainer";

class NearbySearch extends React.Component {
    state = {
        placesList: [],
        isOpen: false
    }
    onToggleOpen(){
        this.setState({isOpen: !this.state.isOpen, ...this.state});
    }
    componentDidMount(){
        const {center, type, map} = this.props;
        var request = {
            location: center,
            rankby: "distance",
            // radius: '500',
            type: [type]
          };
          console.log("in nearby search", map)
          var service = new google.maps.places.PlacesService(map.context.__SECRET_MAP_DO_NOT_USE_OR_YOU_WILL_BE_FIRED);
          service.nearbySearch(request, (result, status) => {
              if (status === google.maps.places.PlacesServiceStatus.OK) {
                console.log("nearby search result", result)
                for (var i = 0; i < result.length; i++) {
                    this.setState({placesList: [...this.state.placesList, result[i]], ...this.state});
                }
          }});
    }
    render(){
        //this.props.setPlacesClicked(false);
        return (this.state.placesList.length && 
            this.state.placesList.map(place => 
                <Marker key={place.place_id} position={place.geometry.location} onClick={this.onToggleOpen}></Marker>, 
                (this.state.isOpen && <InfoWindow onCloseClick={this.onToggleOpen}></InfoWindow>)
            )
        )
    }
}

export default NearbySearch